import React from 'react'
import { graphql } from 'gatsby'
import styled from 'styled-components'
import Layout from '../layout/layout'

import Container from '../layout/Container'
import SuperText from '../components/SuperText'
import SEO from '../components/SEO'
import PostListing from '../components/PostListing'

const RecentPostsContainer = styled.div`
  padding-top: 20vh;
  text-align: left;

  .title {
    font-size: 2em;
    color: var(--text);
  }
`

const IndexPage = ({
  data: {
    allMarkdownRemark: { edges }
  }
}) => (
  <Layout>
    <Container>
      <SEO title="Frontendim - Примеры кода на JS, React, Vue и других фреймворках" />
      <SuperText>Frontendim</SuperText>
      <div>
        <h1>Примеры кода на JS, React, Vue и др.</h1>
        <p>
          Записки внештатного фронтенд-разработчика на фрилансе.
        </p>
        <p>
          Во всех примерах используется Javascript, NodeJS, React,
          В планах Vue и Angular.
        </p>
        <p>Для понимания происходящего, желательно иметь понятие о HTML, CSS, Javascript и React, иначе некоторые вещи вы просто не поймете. Есть много отличных курсов, например вот проверенные:
          <ul>
            <li>https://learn.javascript.ru</li>
            <li>https://www.udemy.com/topic/react</li>
            <li>https://geekbrains.ru/geek_university/frontend</li>
          </ul>
            ... и многие другие. На том же Youtube очень много матариала для изучения.
        </p>
        <RecentPostsContainer>
          <h2 className="title">Последние записи</h2>
          <div id="recent-posts">
            {edges.map(post => (
              <PostListing key={post.node.id} {...post.node.frontmatter} />
            ))}
          </div>
        </RecentPostsContainer>
      </div>
    </Container>
  </Layout>
)

export default IndexPage

export const pageQuery = graphql`
  {
    allMarkdownRemark(
      limit: 3
      filter:{fileAbsolutePath: {regex: "/(\/content\/posts)/.*\\.md$/"}}, 
      sort: { fields: frontmatter___date, order: DESC }
    ) {
      edges {
        node {
          id
          frontmatter {
            slug
            title
            description
            date
          }
        }
      }
    }
  }
`
